"use client";

import { useEffect, useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { getEmployeeStats, checkExpiringDocuments } from "@/lib/employeeStorage";
import { Alert, AlertDescription } from "@/components/ui/alert";
import EmployeesList from "./EmployeesList";

interface Stats {
  total: number;
  active: number;
  temporaryWorkPermit: number;
  companyVisa: number;
}

export default function Dashboard() {
  const [stats, setStats] = useState<Stats>({
    total: 0,
    active: 0,
    temporaryWorkPermit: 0,
    companyVisa: 0,
  });
  const [expiringDocs, setExpiringDocs] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [filterModalOpen, setFilterModalOpen] = useState(false);
  const [selectedFilter, setSelectedFilter] = useState<'active' | 'temporaryWorkPermit' | 'companyVisa' | null>(null);

  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = () => {
    try {
      const employeeStats = getEmployeeStats();
      const expiring = checkExpiringDocuments(30);
      
      setStats(employeeStats);
      setExpiringDocs(expiring);
    } catch (error) {
      console.error('Error loading dashboard data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleStatClick = (filterType: 'active' | 'temporaryWorkPermit' | 'companyVisa') => {
    setSelectedFilter(filterType);
    setFilterModalOpen(true);
  };

  const handleModalClose = () => {
    setFilterModalOpen(false);
    setSelectedFilter(null);
  };

  const handleEmployeeUpdated = () => {
    loadDashboardData();
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-lg text-gray-600">Loading dashboard...</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card className="bg-white shadow-sm border border-gray-200">
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-gray-600">Total Employees</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-gray-900">{stats.total}</div>
            <p className="text-sm text-gray-500 mt-1">All time employees</p>
          </CardContent>
        </Card>

        <Card 
          className="bg-white shadow-sm border border-gray-200 cursor-pointer hover:shadow-md transition-shadow"
          onClick={() => handleStatClick('active')}
        >
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-gray-600">Active Employees</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-green-600">{stats.active}</div>
            <p className="text-sm text-gray-500 mt-1">Currently working  Click to view</p>
          </CardContent>
        </Card>

        <Card 
          className="bg-white shadow-sm border border-gray-200 cursor-pointer hover:shadow-md transition-shadow"
          onClick={() => handleStatClick('temporaryWorkPermit')}
        >
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-gray-600">Temporary Work Permits</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-orange-600">{stats.temporaryWorkPermit}</div>
            <p className="text-sm text-gray-500 mt-1">Temporary permits  Click to view</p>
          </CardContent>
        </Card>

        <Card 
          className="bg-white shadow-sm border border-gray-200 cursor-pointer hover:shadow-md transition-shadow"
          onClick={() => handleStatClick('companyVisa')}
        >
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-gray-600">Company VISAs</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-blue-600">{stats.companyVisa}</div>
            <p className="text-sm text-gray-500 mt-1">Under company name  Click to view</p>
          </CardContent>
        </Card>
      </div>

      {expiringDocs.length > 0 && (
        <div className="space-y-4">
          <h2 className="text-xl font-semibold text-gray-900">Document Expiry Alerts</h2>
          <div className="space-y-3">
            {expiringDocs.map((employee) => (
              <Alert key={employee.id} className="border-orange-200 bg-orange-50">
                <AlertDescription className="text-orange-800">
                  <strong>{employee.fullName}</strong> ({employee.blinkId}) has documents expiring soon.
                  {employee.emiratesIdExpiry && new Date(employee.emiratesIdExpiry) <= new Date(Date.now() + 30 * 24 * 60 * 60 * 1000) && (
                    <span className="block"> Emirates ID expires: {new Date(employee.emiratesIdExpiry).toLocaleDateString()}</span>
                  )}
                  {employee.passportExpiry && new Date(employee.passportExpiry) <= new Date(Date.now() + 30 * 24 * 60 * 60 * 1000) && (
                    <span className="block"> Passport expires: {new Date(employee.passportExpiry).toLocaleDateString()}</span>
                  )}
                  {employee.labourCardExpiry && new Date(employee.labourCardExpiry) <= new Date(Date.now() + 30 * 24 * 60 * 60 * 1000) && (
                    <span className="block"> Labour Card expires: {new Date(employee.labourCardExpiry).toLocaleDateString()}</span>
                  )}
                </AlertDescription>
              </Alert>
            ))}
          </div>
        </div>
      )}

      {expiringDocs.length === 0 && (
        <Card className="bg-green-50 border-green-200">
          <CardContent className="pt-6">
            <div className="text-center">
              <div className="text-green-600 font-medium">All Documents Current</div>
              <p className="text-green-600 text-sm mt-1">No documents expiring in the next 30 days</p>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Filtered Employees Modal */}
      <Dialog open={filterModalOpen} onOpenChange={handleModalClose}>
        <DialogContent className="max-w-6xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>
              {selectedFilter === 'active' && 'Active Employees'}
              {selectedFilter === 'temporaryWorkPermit' && 'Employees with Temporary Work Permits'}
              {selectedFilter === 'companyVisa' && 'Employees with Company VISAs'}
            </DialogTitle>
          </DialogHeader>
          
          {selectedFilter && (
            <EmployeesList 
              filterType={selectedFilter}
              onEmployeeUpdated={handleEmployeeUpdated}
            />
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
}
