"use client";

import { useState } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import Dashboard from "@/components/HR/Dashboard";
import EmployeesList from "@/components/HR/EmployeesList";
import CreateBlinkIdForm from "@/components/HR/CreateBlinkIdForm";

export default function HRPage() {
  const [activeTab, setActiveTab] = useState("dashboard");
  const [refreshEmployees, setRefreshEmployees] = useState(0);

  const handleEmployeeCreated = () => {
    setRefreshEmployees(prev => prev + 1);
    setActiveTab("employees");
  };

  const handleEmployeeUpdated = () => {
    setRefreshEmployees(prev => prev + 1);
  };

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">HR Management System</h1>
          <p className="text-gray-600">Manage employees, track documents, and monitor company statistics</p>
        </div>

        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
          <TabsList className="grid w-full grid-cols-3 mb-8">
            <TabsTrigger value="dashboard" className="text-lg py-3">
              Dashboard
            </TabsTrigger>
            <TabsTrigger value="employees" className="text-lg py-3">
              Employees
            </TabsTrigger>
            <TabsTrigger value="create" className="text-lg py-3">
              Create Blink ID
            </TabsTrigger>
          </TabsList>

          <TabsContent value="dashboard" className="mt-0">
            <Dashboard key={refreshEmployees} />
          </TabsContent>

          <TabsContent value="employees" className="mt-0">
            <EmployeesList 
              key={refreshEmployees} 
              onEmployeeUpdated={handleEmployeeUpdated}
            />
          </TabsContent>

          <TabsContent value="create" className="mt-0">
            <CreateBlinkIdForm onEmployeeCreated={handleEmployeeCreated} />
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
