"use client";

import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

interface BankAccount {
  bankName?: string;
  accountNumber?: string;
  iban?: string;
}

interface BankAccountTabProps {
  bankAccount?: BankAccount;
  onChange: (bankAccount: BankAccount) => void;
}

export default function BankAccountTab({ bankAccount, onChange }: BankAccountTabProps) {
  const [formData, setFormData] = useState<BankAccount>({
    bankName: '',
    accountNumber: '',
    iban: '',
    ...bankAccount
  });

  // UAE Banks list
  const uaeBanks = [
    'Emirates NBD',
    'First Abu Dhabi Bank (FAB)',
    'Abu Dhabi Commercial Bank (ADCB)',
    'Dubai Islamic Bank (DIB)',
    'Mashreq Bank',
    'Commercial Bank of Dubai (CBD)',
    'Union National Bank (UNB)',
    'National Bank of Fujairah (NBF)',
    'Ajman Bank',
    'Bank of Sharjah',
    'National Bank of Ras Al Khaimah (RAKBANK)',
    'United Arab Bank (UAB)',
    'Al Hilal Bank',
    'Noor Bank',
    'Sharjah Islamic Bank',
    'Abu Dhabi Islamic Bank (ADIB)',
    'Emirates Islamic Bank',
    'Invest Bank',
    'National Bank of Umm Al Quwain',
    'Arab Bank',
    'Standard Chartered Bank',
    'HSBC Bank Middle East',
    'Citibank',
    'Other'
  ];

  useEffect(() => {
    setFormData({
      bankName: '',
      accountNumber: '',
      iban: '',
      ...bankAccount
    });
  }, [bankAccount]);

  const handleInputChange = (field: keyof BankAccount, value: string) => {
    const updatedData = {
      ...formData,
      [field]: value
    };
    setFormData(updatedData);
    onChange(updatedData);
  };

  return (
    <div className="space-y-6">
      <div>
        <h3 className="text-lg font-medium text-gray-900 mb-4">Bank Account Information</h3>
        <p className="text-sm text-gray-600 mb-6">
          Manage employee banking details for payroll and financial transactions.
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="space-y-2">
          <Label htmlFor="bankName">Bank Name</Label>
          <Select
            value={formData.bankName || ''}
            onValueChange={(value) => handleInputChange('bankName', value)}
          >
            <SelectTrigger>
              <SelectValue placeholder="Select bank" />
            </SelectTrigger>
            <SelectContent>
              {uaeBanks.map((bank) => (
                <SelectItem key={bank} value={bank}>
                  {bank}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label htmlFor="accountNumber">Account Number</Label>
          <Input
            id="accountNumber"
            placeholder="Enter account number"
            value={formData.accountNumber || ''}
            onChange={(e) => handleInputChange('accountNumber', e.target.value)}
          />
        </div>

        <div className="space-y-2 md:col-span-2">
          <Label htmlFor="iban">IBAN</Label>
          <Input
            id="iban"
            placeholder="AE07 0331 2345 6789 0123 456"
            value={formData.iban || ''}
            onChange={(e) => handleInputChange('iban', e.target.value)}
          />
          <p className="text-xs text-gray-500">
            International Bank Account Number for international transfers
          </p>
        </div>
      </div>

      <div className="bg-gray-50 p-4 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-2">Bank Account Summary</h4>
        <div className="space-y-1 text-sm text-gray-600">
          <div>Bank: {formData.bankName || 'Not specified'}</div>
          <div>Account: {formData.accountNumber || 'Not specified'}</div>
          <div>IBAN: {formData.iban || 'Not specified'}</div>
        </div>
      </div>
    </div>
  );
}
