export interface Employee {
  id: number;
  blinkId: string;
  firstName: string;
  lastName: string;
  fullName: string;
  emiratesId: string;
  emiratesIdExpiry?: string;
  phoneNumber?: string;
  passportNumber?: string;
  passportExpiry?: string;
  labourCardNumber?: string;
  labourCardExpiry?: string;
  mohreId?: string;
  status: 'active' | 'resigned' | 'final settlement' | 'terminated' | 'onboarding';
  statusReason?: string;
  position?: string;
  project?: string;
  emirates?: string;
  salary?: string;
  manager?: string;
  department?: string;
  hasTemporaryWorkPermit: boolean;
  hasCompanyVisa: boolean;
  profilePicture?: string;
  documents?: {
    emiratesIdCopy?: string;
    passportCopy?: string;
    residencyCopy?: string;
    labourCardCopy?: string;
    pnCopy?: string;
    contractCopy?: string;
  };
  bankAccount?: {
    bankName?: string;
    accountNumber?: string;
    iban?: string;
  };
  createdAt: string;
  updatedAt: string;
}

const STORAGE_KEY = 'hr_employees';

export function getEmployees(): Employee[] {
  try {
    if (typeof window === 'undefined') return [];
    const data = localStorage.getItem(STORAGE_KEY);
    return data ? JSON.parse(data) : [];
  } catch (error) {
    console.error('Error reading employees from localStorage:', error);
    return [];
  }
}

export function saveEmployees(employees: Employee[]): void {
  try {
    if (typeof window === 'undefined') return;
    localStorage.setItem(STORAGE_KEY, JSON.stringify(employees));
  } catch (error) {
    console.error('Error saving employees to localStorage:', error);
    throw new Error('Failed to save employee data');
  }
}

export function generateBlinkId(firstName: string, lastName: string, sequenceNumber: number): string {
  const formatName = (name: string): string => {
    const cleaned = name.replace(/[^a-zA-Z]/g, '').toLowerCase();
    return cleaned.charAt(0).toUpperCase() + cleaned.slice(1, 5);
  };

  const formattedFirstName = formatName(firstName);
  const formattedLastName = formatName(lastName);
  
  return `B-${formattedFirstName}-${formattedLastName}${sequenceNumber}`;
}

export function addEmployee(employeeData: Omit<Employee, 'id' | 'blinkId' | 'createdAt' | 'updatedAt'>): Employee {
  try {
    const employees = getEmployees();
    const nextId = employees.length > 0 ? Math.max(...employees.map(e => e.id)) + 1 : 1;
    
    const blinkId = generateBlinkId(employeeData.firstName, employeeData.lastName, nextId);
    
    const newEmployee: Employee = {
      ...employeeData,
      id: nextId,
      blinkId,
      fullName: `${employeeData.firstName} ${employeeData.lastName}`,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    };

    employees.push(newEmployee);
    saveEmployees(employees);
    
    return newEmployee;
  } catch (error) {
    console.error('Error adding employee:', error);
    throw new Error('Failed to add employee');
  }
}

export function updateEmployee(employeeId: number, updatedData: Partial<Employee>): Employee {
  try {
    const employees = getEmployees();
    const employeeIndex = employees.findIndex(e => e.id === employeeId);
    
    if (employeeIndex === -1) {
      throw new Error('Employee not found');
    }

    const updatedEmployee = {
      ...employees[employeeIndex],
      ...updatedData,
      updatedAt: new Date().toISOString(),
    };

    employees[employeeIndex] = updatedEmployee;
    saveEmployees(employees);
    
    return updatedEmployee;
  } catch (error) {
    console.error('Error updating employee:', error);
    throw new Error('Failed to update employee');
  }
}

export function deleteEmployee(employeeId: number): void {
  try {
    const employees = getEmployees();
    const filteredEmployees = employees.filter(e => e.id !== employeeId);
    saveEmployees(filteredEmployees);
  } catch (error) {
    console.error('Error deleting employee:', error);
    throw new Error('Failed to delete employee');
  }
}

export function getEmployeeById(employeeId: number): Employee | null {
  try {
    const employees = getEmployees();
    return employees.find(e => e.id === employeeId) || null;
  } catch (error) {
    console.error('Error getting employee by ID:', error);
    return null;
  }
}

export function getEmployeeStats() {
  try {
    const employees = getEmployees();
    
    return {
      total: employees.length,
      active: employees.filter(e => e.status === 'active').length,
      temporaryWorkPermit: employees.filter(e => e.hasTemporaryWorkPermit).length,
      companyVisa: employees.filter(e => e.hasCompanyVisa).length,
    };
  } catch (error) {
    console.error('Error calculating employee stats:', error);
    return {
      total: 0,
      active: 0,
      temporaryWorkPermit: 0,
      companyVisa: 0,
    };
  }
}

export function checkExpiringDocuments(daysAhead: number = 30): Employee[] {
  try {
    const employees = getEmployees();
    const today = new Date();
    const checkDate = new Date(today.getTime() + (daysAhead * 24 * 60 * 60 * 1000));
    
    return employees.filter(employee => {
      const passportExpiry = employee.passportExpiry ? new Date(employee.passportExpiry) : null;
      const labourCardExpiry = employee.labourCardExpiry ? new Date(employee.labourCardExpiry) : null;
      const emiratesIdExpiry = employee.emiratesIdExpiry ? new Date(employee.emiratesIdExpiry) : null;
      
      return (passportExpiry && passportExpiry <= checkDate) || 
             (labourCardExpiry && labourCardExpiry <= checkDate) ||
             (emiratesIdExpiry && emiratesIdExpiry <= checkDate);
    });
  } catch (error) {
    console.error('Error checking expiring documents:', error);
    return [];
  }
}

export function searchEmployees(query: string): Employee[] {
  try {
    const employees = getEmployees();
    const searchTerm = query.toLowerCase().trim();
    
    if (!searchTerm) return employees;
    
    return employees.filter(employee => {
      // Search by full Blink ID
      if (employee.blinkId.toLowerCase().includes(searchTerm)) {
        return true;
      }
      
      // Search by number after the ID (extract number from Blink ID)
      const blinkIdNumber = employee.blinkId.match(/\d+$/);
      if (blinkIdNumber && blinkIdNumber[0].includes(searchTerm)) {
        return true;
      }
      
      return false;
    });
  } catch (error) {
    console.error('Error searching employees:', error);
    return [];
  }
}

export function getFilteredEmployees(filter: 'active' | 'temporaryWorkPermit' | 'companyVisa'): Employee[] {
  try {
    const employees = getEmployees();
    
    switch (filter) {
      case 'active':
        return employees.filter(e => e.status === 'active');
      case 'temporaryWorkPermit':
        return employees.filter(e => e.hasTemporaryWorkPermit);
      case 'companyVisa':
        return employees.filter(e => e.hasCompanyVisa);
      default:
        return employees;
    }
  } catch (error) {
    console.error('Error filtering employees:', error);
    return [];
  }
}
