"use client";

import { useEffect, useState } from "react";
import { getEmployees, Employee, searchEmployees, getFilteredEmployees } from "@/lib/employeeStorage";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import EmployeeCard from "./EmployeeCard";
import EmployeeDetailsModal from "./EmployeeDetailsModal";

interface EmployeesListProps {
  onEmployeeUpdated: () => void;
  filterType?: 'active' | 'temporaryWorkPermit' | 'companyVisa' | null;
}

export default function EmployeesList({ onEmployeeUpdated, filterType = null }: EmployeesListProps) {
  const [allEmployees, setAllEmployees] = useState<Employee[]>([]);
  const [employees, setEmployees] = useState<Employee[]>([]);
  const [selectedEmployee, setSelectedEmployee] = useState<Employee | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    loadEmployees();
  }, []);

  useEffect(() => {
    applyFiltersAndSearch();
  }, [allEmployees, searchQuery, filterType]);

  const loadEmployees = () => {
    try {
      const employeeData = getEmployees();
      setAllEmployees(employeeData);
    } catch (error) {
      console.error('Error loading employees:', error);
    } finally {
      setLoading(false);
    }
  };

  const applyFiltersAndSearch = () => {
    let filteredEmployees = allEmployees;

    // Apply filter type if specified
    if (filterType) {
      filteredEmployees = getFilteredEmployees(filterType);
    }

    // Apply search if query exists
    if (searchQuery.trim()) {
      filteredEmployees = searchEmployees(searchQuery);
      // If we have both filter and search, we need to apply both
      if (filterType) {
        const searchResults = searchEmployees(searchQuery);
        const filterResults = getFilteredEmployees(filterType);
        filteredEmployees = searchResults.filter(emp => 
          filterResults.some(filtered => filtered.id === emp.id)
        );
      }
    }

    setEmployees(filteredEmployees);
  };

  const handleSearch = (query: string) => {
    setSearchQuery(query);
  };

  const clearSearch = () => {
    setSearchQuery('');
  };

  const handleEmployeeClick = (employee: Employee) => {
    setSelectedEmployee(employee);
    setIsModalOpen(true);
  };

  const handleModalClose = () => {
    setIsModalOpen(false);
    setSelectedEmployee(null);
  };

  const handleEmployeeUpdate = () => {
    loadEmployees();
    onEmployeeUpdated();
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-lg text-gray-600">Loading employees...</div>
      </div>
    );
  }

  const getTitle = () => {
    if (filterType === 'active') return 'Active Employees';
    if (filterType === 'temporaryWorkPermit') return 'Employees with Temporary Work Permits';
    if (filterType === 'companyVisa') return 'Employees with Company VISAs';
    return 'All Employees';
  };

  if (employees.length === 0 && !searchQuery && !filterType) {
    return (
      <div className="text-center py-12">
        <div className="text-gray-500 text-lg mb-4">No employees found</div>
        <p className="text-gray-400">Create your first employee using the "Create Blink ID" tab</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <h2 className="text-2xl font-semibold text-gray-900">
          {getTitle()} ({employees.length})
        </h2>
        
        {!filterType && (
          <div className="flex gap-2 w-full sm:w-auto">
            <div className="relative flex-1 sm:w-80">
              <Input
                placeholder="Search by Blink ID or employee number..."
                value={searchQuery}
                onChange={(e) => handleSearch(e.target.value)}
                className="pr-20"
              />
              {searchQuery && (
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={clearSearch}
                  className="absolute right-1 top-1/2 transform -translate-y-1/2 h-8 px-2 text-gray-400 hover:text-gray-600"
                >
                  Clear
                </Button>
              )}
            </div>
          </div>
        )}
      </div>

      {employees.length === 0 && (searchQuery || filterType) && (
        <div className="text-center py-12">
          <div className="text-gray-500 text-lg mb-4">
            {searchQuery ? 'No employees found matching your search' : 'No employees found for this filter'}
          </div>
          {searchQuery && (
            <p className="text-gray-400 mb-4">
              Try searching with a different Blink ID or employee number
            </p>
          )}
          <Button variant="outline" onClick={clearSearch}>
            {searchQuery ? 'Clear Search' : 'View All Employees'}
          </Button>
        </div>
      )}

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
        {employees.map((employee) => (
          <EmployeeCard
            key={employee.id}
            employee={employee}
            onClick={() => handleEmployeeClick(employee)}
          />
        ))}
      </div>

      {selectedEmployee && (
        <EmployeeDetailsModal
          employee={selectedEmployee}
          isOpen={isModalOpen}
          onClose={handleModalClose}
          onUpdate={handleEmployeeUpdate}
        />
      )}
    </div>
  );
}
