"use client";

import { useState, useEffect } from "react";
import { Employee, updateEmployee } from "@/lib/employeeStorage";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Alert, AlertDescription } from "@/components/ui/alert";
import BankAccountTab from "./BankAccountTab";

interface EmployeeDetailsModalProps {
  employee: Employee;
  isOpen: boolean;
  onClose: () => void;
  onUpdate: () => void;
}

export default function EmployeeDetailsModal({ 
  employee, 
  isOpen, 
  onClose, 
  onUpdate 
}: EmployeeDetailsModalProps) {
  const [formData, setFormData] = useState<Employee>(employee);
  const [error, setError] = useState<string>("");
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    setFormData(employee);
    setError("");
  }, [employee]);

  const handleInputChange = (field: keyof Employee, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleBankAccountChange = (bankData: any) => {
    setFormData(prev => ({
      ...prev,
      bankAccount: bankData
    }));
  };

  const handleSave = async () => {
    try {
      setSaving(true);
      setError("");
      
      await updateEmployee(employee.id, formData);
      onUpdate();
      onClose();
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to update employee");
    } finally {
      setSaving(false);
    }
  };

  const statusOptions = [
    { value: 'active', label: 'Active' },
    { value: 'resigned', label: 'Resigned' },
    { value: 'final settlement', label: 'Final Settlement' },
    { value: 'terminated', label: 'Terminated' },
    { value: 'onboarding', label: 'Onboarding' }
  ];

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="text-xl font-semibold">
            {formData.fullName} - {formData.blinkId}
          </DialogTitle>
        </DialogHeader>

        {error && (
          <Alert className="border-red-200 bg-red-50">
            <AlertDescription className="text-red-800">{error}</AlertDescription>
          </Alert>
        )}

        <Tabs defaultValue="details" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="details">Employee Details</TabsTrigger>
            <TabsTrigger value="bank">Bank Account</TabsTrigger>
          </TabsList>

          <TabsContent value="details" className="space-y-6 mt-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {/* Basic Information */}
              <div className="space-y-4">
                <h3 className="text-lg font-medium text-gray-900">Basic Information</h3>
                
                <div className="space-y-2">
                  <Label htmlFor="fullName">Full Name</Label>
                  <Input
                    id="fullName"
                    value={formData.fullName}
                    onChange={(e) => handleInputChange('fullName', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="phoneNumber">Phone Number</Label>
                  <Input
                    id="phoneNumber"
                    value={formData.phoneNumber || ''}
                    onChange={(e) => handleInputChange('phoneNumber', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="emiratesId">Emirates ID Number</Label>
                  <Input
                    id="emiratesId"
                    value={formData.emiratesId}
                    onChange={(e) => handleInputChange('emiratesId', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="emiratesIdExpiry">Emirates ID Expiry Date</Label>
                  <Input
                    id="emiratesIdExpiry"
                    type="date"
                    value={formData.emiratesIdExpiry || ''}
                    onChange={(e) => handleInputChange('emiratesIdExpiry', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="position">Position</Label>
                  <Input
                    id="position"
                    value={formData.position || ''}
                    onChange={(e) => handleInputChange('position', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="status">Status</Label>
                  <Select
                    value={formData.status}
                    onValueChange={(value) => handleInputChange('status', value)}
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {statusOptions.map((option) => (
                        <SelectItem key={option.value} value={option.value}>
                          {option.label}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {(formData.status === 'resigned' || formData.status === 'terminated' || formData.status === 'final settlement') && (
                  <div className="space-y-2">
                    <Label htmlFor="statusReason">Reason for Status Change</Label>
                    <Input
                      id="statusReason"
                      value={formData.statusReason || ''}
                      onChange={(e) => handleInputChange('statusReason', e.target.value)}
                      placeholder="Enter reason for status change"
                    />
                  </div>
                )}
                
                {formData.status === 'active' && formData.statusReason && (
                  <div className="space-y-2">
                    <Label htmlFor="previousStatusReason">Previous Status Reason (Read Only)</Label>
                    <Input
                      id="previousStatusReason"
                      value={formData.statusReason || ''}
                      readOnly
                      className="bg-gray-50"
                    />
                    <p className="text-xs text-gray-500">This field shows the reason from when the employee was previously inactive</p>
                  </div>
                )}
              </div>

              {/* Document Information */}
              <div className="space-y-4">
                <h3 className="text-lg font-medium text-gray-900">Document Information</h3>
                
                <div className="space-y-2">
                  <Label htmlFor="passportNumber">Passport Number</Label>
                  <Input
                    id="passportNumber"
                    value={formData.passportNumber || ''}
                    onChange={(e) => handleInputChange('passportNumber', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="passportExpiry">Passport Expiry Date</Label>
                  <Input
                    id="passportExpiry"
                    type="date"
                    value={formData.passportExpiry || ''}
                    onChange={(e) => handleInputChange('passportExpiry', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="labourCardNumber">Labour Card Number</Label>
                  <Input
                    id="labourCardNumber"
                    value={formData.labourCardNumber || ''}
                    onChange={(e) => handleInputChange('labourCardNumber', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="labourCardExpiry">Labour Card Expiry Date</Label>
                  <Input
                    id="labourCardExpiry"
                    type="date"
                    value={formData.labourCardExpiry || ''}
                    onChange={(e) => handleInputChange('labourCardExpiry', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="mohreId">MOHRE Employee ID</Label>
                  <Input
                    id="mohreId"
                    value={formData.mohreId || ''}
                    onChange={(e) => handleInputChange('mohreId', e.target.value)}
                  />
                </div>
              </div>
            </div>

            {/* Work Information */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900">Work Information</h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="department">Department</Label>
                  <Input
                    id="department"
                    value={formData.department || ''}
                    onChange={(e) => handleInputChange('department', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="manager">Manager</Label>
                  <Input
                    id="manager"
                    value={formData.manager || ''}
                    onChange={(e) => handleInputChange('manager', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="project">Project</Label>
                  <Input
                    id="project"
                    value={formData.project || ''}
                    onChange={(e) => handleInputChange('project', e.target.value)}
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="salary">Salary</Label>
                  <Input
                    id="salary"
                    value={formData.salary || ''}
                    onChange={(e) => handleInputChange('salary', e.target.value)}
                    placeholder="e.g., 5,000 - 8,000 AED"
                  />
                </div>
              </div>

              <div className="flex gap-4">
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    checked={formData.hasTemporaryWorkPermit}
                    onChange={(e) => handleInputChange('hasTemporaryWorkPermit', e.target.checked)}
                    className="rounded border-gray-300"
                  />
                  <span className="text-sm">Has Temporary Work Permit</span>
                </label>

                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    checked={formData.hasCompanyVisa}
                    onChange={(e) => handleInputChange('hasCompanyVisa', e.target.checked)}
                    className="rounded border-gray-300"
                  />
                  <span className="text-sm">Has Company VISA</span>
                </label>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="bank" className="mt-6">
            <BankAccountTab
              bankAccount={formData.bankAccount}
              onChange={handleBankAccountChange}
            />
          </TabsContent>
        </Tabs>

        <div className="flex justify-end gap-3 pt-6 border-t">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={handleSave} disabled={saving}>
            {saving ? "Saving..." : "Save Changes"}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
