"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { addEmployee, generateBlinkId, getEmployees } from "@/lib/employeeStorage";

interface CreateBlinkIdFormProps {
  onEmployeeCreated: () => void;
}

interface FormData {
  firstName: string;
  lastName: string;
  fullName: string;
  emiratesId: string;
  emiratesIdExpiry: string;
  project: string;
  emirates: string;
  salary: string;
  manager: string;
  department: string;
}

export default function CreateBlinkIdForm({ onEmployeeCreated }: CreateBlinkIdFormProps) {
  const [formData, setFormData] = useState<FormData>({
    firstName: '',
    lastName: '',
    fullName: '',
    emiratesId: '',
    emiratesIdExpiry: '',
    project: '',
    emirates: '',
    salary: '',
    manager: '',
    department: ''
  });

  const [errors, setErrors] = useState<Partial<FormData>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitError, setSubmitError] = useState('');
  const [previewBlinkId, setPreviewBlinkId] = useState('');

  // Dropdown options
  const projectOptions = [
    'Project Alpha', 'Project Beta', 'Project Gamma', 'Project Delta', 
    'Construction Site A', 'Construction Site B', 'Office Building C'
  ];

  const emiratesOptions = [
    'Abu Dhabi', 'Dubai', 'Sharjah', 'Ajman', 'Ras Al Khaimah', 'Fujairah', 'Umm Al Quwain'
  ];

  const salaryRanges = [
    '1,000 - 3,000 AED', '3,000 - 5,000 AED', '5,000 - 8,000 AED', 
    '8,000 - 12,000 AED', '12,000 - 20,000 AED', '20,000+ AED'
  ];

  const managerOptions = [
    'Ahmed Al Mansouri', 'Sarah Johnson', 'Mohammed Hassan', 'Lisa Chen', 
    'Omar Abdullah', 'Jennifer Smith', 'Khalid Al Zaabi'
  ];

  const departmentOptions = [
    'Human Resources', 'Engineering', 'Construction', 'Finance', 'Marketing', 
    'Operations', 'IT', 'Legal', 'Administration'
  ];

  const handleInputChange = (field: keyof FormData, value: string) => {
    const updatedData = { ...formData, [field]: value };
    
    // Auto-generate full name when first or last name changes
    if (field === 'firstName' || field === 'lastName') {
      const firstName = field === 'firstName' ? value : updatedData.firstName;
      const lastName = field === 'lastName' ? value : updatedData.lastName;
      updatedData.fullName = `${firstName} ${lastName}`.trim();
    }
    
    setFormData(updatedData);
    
    // Clear error for this field
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }

    // Update preview Blink ID
    if (field === 'firstName' || field === 'lastName') {
      updateBlinkIdPreview(updatedData);
    }
  };

  const updateBlinkIdPreview = (data: FormData) => {
    if (data.firstName && data.lastName) {
      const employees = getEmployees();
      const nextId = employees.length > 0 ? Math.max(...employees.map(e => e.id)) + 1 : 1;
      const preview = generateBlinkId(data.firstName, data.lastName, nextId);
      setPreviewBlinkId(preview);
    } else {
      setPreviewBlinkId('');
    }
  };

  const validateForm = (): boolean => {
    const newErrors: Partial<FormData> = {};

    if (!formData.firstName.trim()) {
      newErrors.firstName = 'First name is required';
    }

    if (!formData.lastName.trim()) {
      newErrors.lastName = 'Last name is required';
    }

    if (!formData.fullName.trim()) {
      newErrors.fullName = 'Full name is required';
    }

    if (!formData.emiratesId.trim()) {
      newErrors.emiratesId = 'Emirates ID is required';
    } else if (!/^\d{3}-\d{4}-\d{7}-\d{1}$/.test(formData.emiratesId)) {
      newErrors.emiratesId = 'Emirates ID format should be XXX-XXXX-XXXXXXX-X';
    }

    if (!formData.emiratesIdExpiry.trim()) {
      newErrors.emiratesIdExpiry = 'Emirates ID expiry date is required';
    }

    if (!formData.project) {
      newErrors.project = 'Project selection is required';
    }

    if (!formData.emirates) {
      newErrors.emirates = 'Emirates selection is required';
    }

    if (!formData.department) {
      newErrors.department = 'Department selection is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);
    setSubmitError('');

    try {
      const newEmployee = await addEmployee({
        firstName: formData.firstName.trim(),
        lastName: formData.lastName.trim(),
        fullName: formData.fullName.trim(),
        emiratesId: formData.emiratesId.trim(),
        emiratesIdExpiry: formData.emiratesIdExpiry,
        project: formData.project,
        emirates: formData.emirates,
        salary: formData.salary,
        manager: formData.manager,
        department: formData.department,
        status: 'onboarding',
        hasTemporaryWorkPermit: false,
        hasCompanyVisa: false,
      });

      // Reset form
      setFormData({
        firstName: '',
        lastName: '',
        fullName: '',
        emiratesId: '',
        emiratesIdExpiry: '',
        project: '',
        emirates: '',
        salary: '',
        manager: '',
        department: ''
      });
      setPreviewBlinkId('');
      
      // Notify parent component
      onEmployeeCreated();
      
    } catch (error) {
      setSubmitError(error instanceof Error ? error.message : 'Failed to create employee');
    } finally {
      setIsSubmitting(false);
    }
  };

  const resetForm = () => {
    setFormData({
      firstName: '',
      lastName: '',
      fullName: '',
      emiratesId: '',
      emiratesIdExpiry: '',
      project: '',
      emirates: '',
      salary: '',
      manager: '',
      department: ''
    });
    setErrors({});
    setPreviewBlinkId('');
    setSubmitError('');
  };

  return (
    <div className="max-w-4xl mx-auto">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-semibold text-gray-900">
            Create New Employee Blink ID
          </CardTitle>
          <p className="text-gray-600">
            Fill in the employee details to generate a new Blink ID automatically
          </p>
        </CardHeader>
        
        <CardContent>
          {submitError && (
            <Alert className="mb-6 border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">{submitError}</AlertDescription>
            </Alert>
          )}

          {previewBlinkId && (
            <div className="mb-6 p-4 bg-blue-50 border border-blue-200 rounded-lg">
              <div className="text-sm font-medium text-blue-900 mb-1">Preview Blink ID:</div>
              <div className="text-lg font-mono text-blue-700">{previewBlinkId}</div>
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Personal Information */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900">Personal Information</h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="firstName">First Name *</Label>
                  <Input
                    id="firstName"
                    value={formData.firstName}
                    onChange={(e) => handleInputChange('firstName', e.target.value)}
                    className={errors.firstName ? 'border-red-500' : ''}
                    placeholder="Enter first name"
                  />
                  {errors.firstName && (
                    <p className="text-sm text-red-600">{errors.firstName}</p>
                  )}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="lastName">Last Name *</Label>
                  <Input
                    id="lastName"
                    value={formData.lastName}
                    onChange={(e) => handleInputChange('lastName', e.target.value)}
                    className={errors.lastName ? 'border-red-500' : ''}
                    placeholder="Enter last name"
                  />
                  {errors.lastName && (
                    <p className="text-sm text-red-600">{errors.lastName}</p>
                  )}
                </div>
              </div>

                <div className="space-y-2">
                  <Label htmlFor="fullName">Full Name *</Label>
                  <Input
                    id="fullName"
                    value={formData.fullName}
                    onChange={(e) => handleInputChange('fullName', e.target.value)}
                    className={errors.fullName ? 'border-red-500' : ''}
                    placeholder="Full name will be auto-generated or you can edit it"
                  />
                  {errors.fullName && (
                    <p className="text-sm text-red-600">{errors.fullName}</p>
                  )}
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="emiratesId">Emirates ID Number *</Label>
                  <Input
                    id="emiratesId"
                    value={formData.emiratesId}
                    onChange={(e) => handleInputChange('emiratesId', e.target.value)}
                    className={errors.emiratesId ? 'border-red-500' : ''}
                    placeholder="XXX-XXXX-XXXXXXX-X"
                  />
                  {errors.emiratesId && (
                    <p className="text-sm text-red-600">{errors.emiratesId}</p>
                  )}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="emiratesIdExpiry">Emirates ID Expiry Date *</Label>
                  <Input
                    id="emiratesIdExpiry"
                    type="date"
                    value={formData.emiratesIdExpiry}
                    onChange={(e) => handleInputChange('emiratesIdExpiry', e.target.value)}
                    className={errors.emiratesIdExpiry ? 'border-red-500' : ''}
                  />
                  {errors.emiratesIdExpiry && (
                    <p className="text-sm text-red-600">{errors.emiratesIdExpiry}</p>
                  )}
                </div>
            </div>

            {/* Work Information */}
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-900">Work Information</h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="project">Project *</Label>
                  <Select
                    value={formData.project}
                    onValueChange={(value) => handleInputChange('project', value)}
                  >
                    <SelectTrigger className={errors.project ? 'border-red-500' : ''}>
                      <SelectValue placeholder="Select project" />
                    </SelectTrigger>
                    <SelectContent>
                      {projectOptions.map((option) => (
                        <SelectItem key={option} value={option}>
                          {option}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.project && (
                    <p className="text-sm text-red-600">{errors.project}</p>
                  )}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="emirates">Emirates *</Label>
                  <Select
                    value={formData.emirates}
                    onValueChange={(value) => handleInputChange('emirates', value)}
                  >
                    <SelectTrigger className={errors.emirates ? 'border-red-500' : ''}>
                      <SelectValue placeholder="Select emirates" />
                    </SelectTrigger>
                    <SelectContent>
                      {emiratesOptions.map((option) => (
                        <SelectItem key={option} value={option}>
                          {option}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.emirates && (
                    <p className="text-sm text-red-600">{errors.emirates}</p>
                  )}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="salary">Salary Range</Label>
                  <Select
                    value={formData.salary}
                    onValueChange={(value) => handleInputChange('salary', value)}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select salary range" />
                    </SelectTrigger>
                    <SelectContent>
                      {salaryRanges.map((option) => (
                        <SelectItem key={option} value={option}>
                          {option}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="manager">Manager</Label>
                  <Select
                    value={formData.manager}
                    onValueChange={(value) => handleInputChange('manager', value)}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select manager" />
                    </SelectTrigger>
                    <SelectContent>
                      {managerOptions.map((option) => (
                        <SelectItem key={option} value={option}>
                          {option}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2 md:col-span-2">
                  <Label htmlFor="department">Department *</Label>
                  <Select
                    value={formData.department}
                    onValueChange={(value) => handleInputChange('department', value)}
                  >
                    <SelectTrigger className={errors.department ? 'border-red-500' : ''}>
                      <SelectValue placeholder="Select department" />
                    </SelectTrigger>
                    <SelectContent>
                      {departmentOptions.map((option) => (
                        <SelectItem key={option} value={option}>
                          {option}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.department && (
                    <p className="text-sm text-red-600">{errors.department}</p>
                  )}
                </div>
              </div>
            </div>

            {/* Form Actions */}
            <div className="flex justify-end gap-3 pt-6 border-t">
              <Button
                type="button"
                variant="outline"
                onClick={resetForm}
                disabled={isSubmitting}
              >
                Reset Form
              </Button>
              <Button
                type="submit"
                disabled={isSubmitting}
              >
                {isSubmitting ? 'Creating...' : 'Create Employee'}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
