"use client";

import { Employee } from "@/lib/employeeStorage";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

interface EmployeeCardProps {
  employee: Employee;
  onClick: () => void;
}

export default function EmployeeCard({ employee, onClick }: EmployeeCardProps) {
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active':
        return 'bg-green-100 text-green-800 border-green-200';
      case 'resigned':
        return 'bg-gray-100 text-gray-800 border-gray-200';
      case 'terminated':
        return 'bg-red-100 text-red-800 border-red-200';
      case 'onboarding':
        return 'bg-blue-100 text-blue-800 border-blue-200';
      case 'final settlement':
        return 'bg-orange-100 text-orange-800 border-orange-200';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  return (
    <Card 
      className="cursor-pointer hover:shadow-lg transition-shadow duration-200 bg-white border border-gray-200"
      onClick={onClick}
    >
      <CardContent className="p-4 text-center">
        <div className="mb-4">
          <img
            src={employee.profilePicture || "https://placehold.co/120x120?text=Employee+Photo+Not+Available"}
            alt={`${employee.fullName} profile picture`}
            className="w-20 h-20 rounded-full mx-auto object-cover border-2 border-gray-200"
            onError={(e) => {
              const target = e.target as HTMLImageElement;
              target.src = "https://placehold.co/120x120?text=Employee+Photo+Not+Available";
            }}
          />
        </div>
        
        <div className="space-y-2">
          <h3 className="font-semibold text-gray-900 text-sm truncate">
            {employee.fullName}
          </h3>
          
          <div className="text-xs font-mono text-blue-600 bg-blue-50 px-2 py-1 rounded">
            {employee.blinkId}
          </div>
          
          <Badge 
            variant="outline" 
            className={`text-xs ${getStatusColor(employee.status)}`}
          >
            {employee.status}
          </Badge>
          
          {employee.position && (
            <div className="text-xs text-gray-500 truncate">
              {employee.position}
            </div>
          )}
          
          {employee.department && (
            <div className="text-xs text-gray-400 truncate">
              {employee.department}
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
